.modal {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(#000, 0.52);
	left: 0;
	top: 0;
	z-index: 99;
	display: none;
	&_visible {
	 display: flex; }
	&__dialog {
	 width: 1170px;
	 height: 674px;
	 background-color: #fff;
	 margin: auto;
	 display: flex;
	 justify-content: center;
	 align-items: center;
	 position: relative; }
	&__close {
	 position: absolute;
	 top: 15px;
	 right: 15px;
	 &:hover {
	  cursor: pointer; } }
	&__main {
	 display: grid;
	 grid-template-columns: 600px 570px;
	 grid-template-rows: 674px; } }
