.portfolio {

 padding: 100px 0;
 background: url(../img/portfolio/portfolio-bg.png) no-repeat left;
 background-color: $light-bg-color; }

.slider {
	&__item {
	 text-align: center;
	 img {
	  margin: auto; } } }
.section-title {
	margin-bottom: 99px; }
.arrow {
	 width: 45px;
	 height: 19px;
	 position: absolute;
	 bottom: -68px;
	 &:hover {
	  cursor: pointer; }
	 &_right {
	  left: 50%;
	  background: url(../img/portfolio/arrow-right.png) no-repeat center;
	  background-size: 100%;
	  transform: translateX(15px); }
	 &_left {
	  left: 50%;
	  transform: translateX(-45px);
	  margin-left: -30px;
	  background: url(../img/portfolio/arrow-left.png) no-repeat center;
	  background-size: 100%; } }

